import popupMessage from "@/components/popupMessage.vue"
export default {
	components:{
		popupMessage
	},
	data() {
		return {
			ablityShut:'',
			type: 1,
			price:'',
			record:[],
			isActive: true,
			level: [],
			selectIndex: -1,
			record:[],
			sliceIndex: 1,
			sliceArr: [{id: 1,name: '靜態認購'},{id: 2,name: '動態認購'}],
			userDetail:'',
			storeRate: 0,
			pageNum: 1,
			pageSize: 20,
			flowType:['认购'],
			content:'',
			
			
		};
	},
	onNavigationBarButtonTap:function(val){
		 
		this.tapSettingButton() 
	    
	},
	onLoad:function(){
		this.fetchShut()
		this.fetchPrice()
		this.fetchList()
		this.fetchList11()
		this.fetchUserDetail()
	},
	methods:{
		async fetchShut(){
			const res = await this.$http.get('/user/main_shunt')
			this.ablityShut = res.data.data.msg
			console.log(res)
		},
		tapSettingButton:function(){
			this.showDialog(4)
		},
		toDealHall:function(){
			if(this.userDetail.phone == ''){
				// uni.showToast({
				// 	title: '請先綁定手機號碼',
				// 	icon: 'none'
				// });
				this.showDialog(5)
				
			}else {
				uni.navigateTo({
					url: '/pages/usdfDeal/hall'
					
				});
			}
			
			
		},
		showDialog(index){
			this.type = index
			let tmp = ''
			this.level.forEach((item)=>{
				if (item.id == this.selectIndex){
					tmp = item.label
				}
			})
			if (index ==1){
				this.content = `確認認購${tmp}`
			}else if (index ==2){
				this.content = `確認靜態復投${tmp}`
			}else if (index ==3){
				this.content = `確認動態復投${tmp}`
			}else if (index ==4){
				this.content = `需耐心等待排隊時間到期，將自動買入熵值`
			}
			else if (index ==5){
				this.content = `請先綁定手機號碼`
			}
			
			this.$refs.dialog.show()
		},
		confirmInput:function(type){
			console.log(type)
			// this.secondPassword = e
			// console.log(this.secondPassword)
			if (type  ==1){
				this.buyStore()
			}else if (type ==2){
				this.buyStore()
			}else if (type ==3){
				this.dynicRenew()
			}else if (type ==5){
				uni.navigateTo({
					url: '/pages/userCenter/secureCenter/secureCenter'
					
				});
			}
			
		},
		async fetchChaifen(){
			
			const res = await this.$http.get('/user/stock_flow')
			let tmp = res.data.data.list
			tmp.forEach((item)=>{
				this.$set(item,'details','配送')
				this.$set(item,'usde',item.stockCount)
				item.createTime = this.handleDate(item.createTime)
			})
			this.record = tmp
			console.log(res)
		},
		//动态复投	
		async dynicRenew(){
			if (this.selectIndex == -1){
				uni.showToast({
					title: '請選擇購買等級',
					icon: 'none'
				});
				return
			}
			let data ={
				level: this.selectIndex
			}
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.get('/dm/anew',{data: data})
			uni.hideLoading()
			uni.showToast({
				title: '复投成功',
				duration: 2000
			});
			// setTimeout(function() {
			// 	uni.navigateBack({
			// 		delta:  -1
			// 	});
			// }, 2000);
		},
		async fetchPrice(){
			
			const res = await this.$http.get('/st/unit_price')
			this.price = res.data.data.msg
			
			console.log(res)
		},
		async fetchList11(){
			// let data ={
			// 	pageNum: this.pageNum,
			// 	pageSize: this.pageSize,
			// 	flowType: this.flowType
			// }
			// const res = await this.$http.get('/user/flow',{data: data})
			
			// this.record = res.data.data.list
			// this.record.forEach((item)=>{
			// 	item.details = '初始源'
			// 	item.usde = -(item.usde/2)
			// 	item.createTime = this.handleDate(item.createTime)
			// })
			// console.log(res)
			let data ={
				type: 0
			}
			const res = await this.$http.get('/st/buy_list',{data: data})
			this.record = res.data.data.list
			this.record.forEach((item)=>{
				item.createTime = this.handleDate(item.createTime)
				if (item.isFinish == 0){
					this.$set(item,'details','排隊中')
					this.$set(item,'usde',item.totalPrice)
				}else {
					this.$set(item,'details','已完成')
					this.$set(item,'usde',item.stockCount)
				}
				
			})
			console.log(res)
		},
		async fetchListDynic(){
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize,
				flowType: this.flowType
			}
			const res = await this.$http.get('/user/flow',{data: data})
			
			this.record = res.data.data.list
			this.record.forEach((item)=>{
				item.details = '进行中'
				item.usde = -(item.usde)
				item.createTime = this.handleDate(item.createTime)
			})
			console.log(res)
		},
		handleDate:function(d){
			let arr = d.split(' ')
			let date1 = arr[0].split('-')
			let date2 = arr[1].split(':')
			
			return date1[1]+'-'+date1[2]+' '+date2[0]+':'+date2[1]
		},
		async fetchUserDetail(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
			if (this.userDetail.staticUserStatus >0){
				this.isActive = false
			}
			
		},
		async buyStore(){
			if (this.selectIndex == -1){
				uni.showToast({
					title: '請選擇購買等級',
					icon: 'none'
				});
				return
			}
			let data ={
				level: this.selectIndex
			}
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.get('/st/share_subscription',{data: data})
			uni.hideLoading()
			uni.showToast({
				title: '认购成功',
				duration: 1000
			});
			setTimeout(function() {
				uni.navigateBack({
					delta:  -1
				});
			}, 2000);
			
		},
		async fetchList(){
			const res = await this.$http.get('/basicdata/user_level')
			this.level = res.data.data.list
			console.log(res)
		},
		selectItem:function(item){
			
			if (!item.canSelect){
				uni.showToast({
					title: item.hint,
					icon: 'none'
				});
				return
			}
			this.selectIndex = item.id
			this.storeRate = item.id/2
			
		},
		tapSlice:function(index){
			this.sliceIndex = index
			if (index == 1){
				this.fetchList11()
			} else {
				this.fetchListDynic()
			}
		}
	}
	
}
